import DeleteIcon from '@mui/icons-material/Delete'
import { Alert, Button, Stack, Typography } from '@mui/material'
import { DataGrid } from '@mui/x-data-grid'
import { GroupApi } from 'API/group'
import DialogConfirm from 'components/DialogConfirm'
import { IGroup, PERMISSION } from 'interfaces'
import { Fragment, useEffect, useState } from 'react'
import { MdReadMore } from 'react-icons/md'
import { Link } from 'react-router-dom'
import { useAppDispatch, useAppSelector } from 'redux/hooks'
import { getGroups } from 'redux/slices/Group'

export default function Group() {
	const status = useAppSelector((s) => s.Group.status)
	const tokenInfo = useAppSelector((s) => s.UI.tokenInfo)
	const list = useAppSelector((s) => s.Group.list)
	const countAccountsInGroup = (group) => {
		return group.accounts.filter((account) => account.status === 'active' || account.status === 'disabled').length
	}
	const dispatch = useAppDispatch()
	useEffect(() => {
		if (status === 'init') dispatch(getGroups())
		// eslint-disable-next-line react-hooks/exhaustive-deps
	}, [])
	const [selected, setSelected] = useState<IGroup>()
	if (!tokenInfo) return null
	return (
		<Stack gap={2}>
			<DialogConfirm
				open={!!selected}
				handleClose={() => {
					setSelected(undefined)
				}}
				handleSubmit={() => {
					if (selected) GroupApi.delete(selected.id)
					setSelected(undefined)
				}}
				maxWidth="xs"
				primaryButtonText="削除"
				primaryButtonColor="error"
			>
				<Typography sx={{ textAlign: 'center' }}>
					<span
						style={{
							whiteSpace: 'nowrap',
							overflow: 'hidden',
							textOverflow: 'ellipsis',
							maxWidth: '60%',
							display: 'inline-block',
							verticalAlign: 'text-bottom',
						}}
					>
						{selected?.name}
					</span>
					を削除しますか？
				</Typography>
				{selected && countAccountsInGroup(selected) > 0 && (
					<Alert severity="warning">このグループに所属するアカウントもすべて同時に削除されます。</Alert>
				)}
			</DialogConfirm>
			<Stack direction="row" alignItems="center" justifyContent="space-between" gap={2} sx={{}}>
				<Typography
					sx={{
						fontWeight: 600,
					}}
					variant="h5"
				>
					全ての組織一覧
				</Typography>
				<Link to="/group/create">
					<Button
						variant="outlined"
						sx={{
							minWidth: 120,
						}}
					>
						新規作成
					</Button>
				</Link>
			</Stack>
			<DataGrid
				rows={list.filter((f) => f.id !== 1)}
				columns={[
					{ field: 'name', headerName: 'グループ名', flex: 3 },
					{ field: 'description', headerName: '説明', flex: 4 },
					{
						field: 'Accounts',
						headerName: 'アカウント数',
						flex: 1,
						valueGetter: (params) => countAccountsInGroup(params.row),
					},
					{
						field: '',
						headerName: 'アクション',
						flex: 2,
						renderCell: (v) => {
							return (
								<Fragment key={v.id}>
									<Link to={`/group/${v.row.id}`}>
										<Button
											sx={{
												mx: 0.5,
												px: 0.5,
												py: 0.2,
												minWidth: 0,
												fontSize: 12,
											}}
											size="small"
											color="info"
											variant="outlined"
										>
											詳細
											<MdReadMore style={{ fontSize: 20, marginTop: 1 }} />
										</Button>
									</Link>
									{tokenInfo.permissions[PERMISSION.ACCOUNT] > 4 && (
										<Button
											sx={{
												mx: 0.5,
												px: 0.5,
												py: 0.2,
												minWidth: 0,
												fontSize: 12,
											}}
											size="small"
											color="error"
											variant="outlined"
											onClick={() => setSelected(v.row)}
										>
											削除
											<DeleteIcon style={{ fontSize: 20, marginTop: 1 }} />
										</Button>
									)}
								</Fragment>
							)
						},
					},
				]}
				initialState={{
					pagination: {
						paginationModel: { page: 0, pageSize: 15 },
					},
				}}
				pageSizeOptions={[15, 30, 50]}
				sx={{
					'& .MuiDataGrid-row': {},
					'& .MuiDataGrid-cell:focus': {
						outline: 'none',
					},
					'& .MuiDataGrid-cell:focus-within': {
						outline: 'none',
					},
					'& .MuiDataGrid-columnHeaders': {
						bgcolor: 'primary.dark',
						color: '#fff',
					},
					'& .MuiDataGrid-columnHeaders svg': {
						color: '#fff',
					},
				}}
				getRowClassName={(v) => {
					return ''
				}}
				density="compact"
				hideFooterSelectedRowCount
				editMode="cell"
				disableColumnMenu
				disableRowSelectionOnClick
			/>
		</Stack>
	)
}
